import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import GoogleLogin from 'react-google-login';
import Modal from '@modules/Modal';
import Preloader from '@components/preloader';
import SojiBaseInit from '@modules/SojiBase';
import Cookies from 'js-cookie';
import Button from './Button';

export default function GoogleLoginHandler({ text, embedToGetUserToken }) {
    const navigate = useNavigate();
    const [state, setState] = useState({
        email: '',
        password: '',
        onRequest: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const responseGoogle = async (response) => {
        if (response.error == 'popup_closed_by_user') {
           return
        }else {
            stateHandler('onRequest', true);
            const userDetails = response.profileObj;
            const { email, familyName, givenName, imageUrl } = userDetails;
            const payload = { email, firstName: givenName, lastName: familyName, profileUrl: imageUrl };

            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.post('v1/authe/oauth', payload);
            getter.on('static', (response) => {
                stateHandler('onRequest', false);
                const data = response.response.data;
                if (data.token) {
                    Cookies.set('SOJI_uft_token', data.token);
                    if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
                        if (!embedToGetUserToken) {
                            navigate('/recent-document', { replace: true });
                        } else {
                            embedToGetUserToken(data.token);
                        }
                    }
                }
            });
        }
    };

    return (
        <>
            <GoogleLogin
                render={(renderProps) => (
                    <Button text={text} onClick={renderProps.onClick} disabled={renderProps.disabled} />
                )}
                clientId="178683516706-bgoi08u14chjoopdmk0rjbk0hfpsb7df.apps.googleusercontent.com"
                buttonText="Login"
                onSuccess={responseGoogle}
                onFailure={responseGoogle}
                cookiePolicy={'single_host_origin'}
            />
            <Modal visible={state.onRequest} onRequestClose={() => stateHandler('onRequest', false)}>
                <Preloader />
            </Modal>
        </>
    );
}
